@media (width > 48em) {
  .col-2 {
    flex: 0 1 calc(50% - 1.5rem);
  }
}

@media (width > 48em) {
  .col-3 {
    flex: 0 1 calc(33.3333333333% - 1.5rem);
  }
}

@media (width > 48em) {
  .col-4 {
    flex: 0 1 calc(50% - 6rem);
  }
}
@media (width > 70em) {
  .col-4 {
    flex: 0 1 calc(25% - 6rem);
  }
}

.navbar {
  font-size: 1.4rem;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  padding-top: 5rem;
  padding-bottom: 1.7rem;
  transition: transform 1s;
  justify-content: space-between;
  align-items: center;
}
.navbar__logo a {
  font-size: 1.6rem;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar__logo img {
  width: 3rem;
}
.navbar__logo p {
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 0.652rem;
}
.navbar__menu {
  font-weight: 400;
  display: none;
  align-items: center;
  justify-content: center;
}
@media (width > 70em) {
  .navbar__menu {
    display: flex;
  }
}
.navbar__menu.openMenuMobile {
  background: rgba(19, 19, 19, 0.8);
  transform: translateY(0);
  opacity: 1;
  transition: all 0.5s ease;
}
.navbar__menu li {
  margin: 1rem;
}
.navbar__menu li a {
  color: #fff;
  text-transform: uppercase;
}
@media (width > 70em) {
  .navbar__menu li:nth-child(6) {
    display: none;
  }
}
.navbar__location {
  display: none;
  font-weight: 400;
}
@media (width > 70em) {
  .navbar__location {
    display: flex;
  }
}
.navbar__location a {
  color: #fff;
  text-transform: uppercase;
}
.navbar__hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 1.9rem;
  cursor: pointer;
}
@media (width > 70em) {
  .navbar__hamburger {
    display: none;
  }
}
.navbar__hamburger span {
  width: 2rem;
  height: 0.3rem;
  background: #fff;
}
.navbar__hamburger.active span:nth-child(1) {
  margin: 8px auto;
  transform: translateY(13px) rotate(45deg);
}
.navbar__hamburger.active span:nth-child(2) {
  opacity: 0;
}
.navbar__hamburger.active span:nth-child(3) {
  margin: 8px auto;
  transform: translateY(-13px) rotate(-45deg);
}